body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(60, 60, 60);
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

/* my custom styles */
.calculator{
  width: 320px;
  position: relative;
  background-color: rgb(184, 170, 150);
}
.formulaScreen{
  color: rgb(233, 231, 221);
  text-align: right;
  height: 45px;
  font-size: 16px;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 8px;
  padding-top: 15px;
}
.outputScreen{
  color: #fff;
  text-align: right;
  height: 45px;
  font-size: 28px;
  padding-right: 15px;
  padding-left: 15px;
}
button{
  width: 80px;
  height: 65px;
  position: relative;
  color: #000;
  background-color: rgb(248, 241, 225);
  border: 1px solid rgba(219, 211, 191, 0.514);
  cursor: pointer;
}
.large-btn#clear, .large-btn#zero{
  width: 160px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

